@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600;700;800;900&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Fraunces:wght@300;400;500;600;700;800;900&display=swap'); */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* html{
    overflow: hidden;
} */

/*! App */
.App 
{
    width: 100%;
    height: 100%;   
    min-height: 100vh;
    /* border: 1px solid red; */

    /* font-family: 'Fraunces', serif; */
    font-family: 'Roboto Slab', serif;
    position: relative;

    overflow-x: hidden;
    background-color: #282729;
}

/*! main show image */
.main_show_image 
{
    /* border: 1px solid red; */
    position: absolute;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    transition: .2s;
}
/* show */
.main_show_image.show
{
    /* border: 1px solid red; */

    position: fixed;
    width: 100%;
    height: 100vh;

    background: rgb(0, 0, 0, 0.7);
    z-index: 100;
    cursor: pointer;
}
/* close */
.main_show_image.close
{
    display: none;
}
/* show image */
.main_show_image .show_image 
{
    width: 500px;
    height: 500px; 
    transition: .2s;
}

/*! Main show input */
/* show */
.main_show_input.show
{
    /* border: 1px solid red; */

    position: absolute;
    width: 100%;
    height: 100%;

    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    
}
.main_show_input.show::before
{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgb(0, 0, 0, 0.5);
    z-index: 100;
}
/* main_show_input_container */
.main_show_input_container 
{
    /* border: 1px solid red; */

    position: fixed;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}
/*! main show input wrapper */
.main_show_input_wrapper 
{
    background: #413f42;    
    box-shadow: 3px 3px 10px rgba(0, 0, 0, .4);
    border: 1px solid #5e5e5e;
    border-radius: 8px;
    /* text-align: center; */
    
    position: relative; 
    overflow: hidden;

    max-width: 100%;
    width: 500px;
    height: auto;
    
    display: flex;

    transition: all .5s;
}
/* close */
.main_show_input.close
{
    display: none;
}
/* header */
.show_input_header {
    /* border: 1px solid red; */
    border-bottom: 1px solid #535353;
    padding: 10px 0px;
    margin: 0 16px 0 16px;
    height: 50px;

    display: flex;
    justify-content: center;
    align-items: center;
    
    position: relative;
}
.show_input_header p 
{
    color: rgb(219, 219, 219);
    font-size: 20px;
    font-weight: 700;
    text-shadow: 3px 3px 10px rgba(0, 0, 0, .4);
}
.show_input_header p::selection
{
    pointer-events: none;

    -ms-user-select: none;

        user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;

    -webkit-user-select: none;
    -webkit-user-drag: none;
    cursor: default;
}
.show_input_header p:hover 
{
    cursor: default;
}
/* btn close */
.btn_close_input 
{
    /* border: 1px solid rgb(0, 162, 255); */

    position: absolute;
    right: 0px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.icon_close_input 
{
    color: rgb(197, 196, 196);
    cursor: pointer;
    font-size: 1.7em;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, .4);
    border-radius: 50%;

    transition: .1s;
}
.icon_close_input:hover 
{
    color: rgb(255, 255, 255);
}

/*! input main */
.show_input_main 
{
    /* border: 1px solid red; */
    position: relative;
    width: 100%;
    padding-right: 5px;
}
/* textarea */
.input_textarea
{
    width: 100%;
    padding: 4px 35px 0 16px;

    color: rgb(223, 223, 223);
    background: #413f42;    
    border: none;
    outline: none;
    font-size: 1.3em;

    resize: none;
    /* border: 1px   solid red; */
    /* min-height: 170px !important; */
}
.input_textarea::selection
{
    background: #5e03c0;
}
.input_textarea::-webkit-input-placeholder
{
    color: rgb(255, 255, 255, .3);
    -webkit-transition: .2s;
    transition: .2s;
    font-size: 1.3em;
}
.input_textarea:-ms-input-placeholder
{
    color: rgb(255, 255, 255, .3);
    -ms-transition: .2s;
    transition: .2s;
    font-size: 1.3em;
}
.input_textarea::placeholder
{
    color: rgb(255, 255, 255, .3);
    transition: .2s;
    font-size: 1.3em;
}
.input_textarea:focus::-webkit-input-placeholder
{
    color: rgb(255, 255, 255, .1);
}
.input_textarea:focus:-ms-input-placeholder
{
    color: rgb(255, 255, 255, .1);
}
.input_textarea:focus::placeholder
{
    color: rgb(255, 255, 255, .1);
}
/* textarea scrollbar */
.input_textarea::-webkit-scrollbar-track
{
	/* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #413f42; */
	border-radius: 10px;
}

.input_textarea::-webkit-scrollbar
{
	width: 8px;
	background-color: #413f42;
}

.input_textarea::-webkit-scrollbar-thumb
{
	background-color: rgb(95, 95, 95);
	border-radius: 10px;
}

/*! emoji picker */
/* container */
.emoji_picker_container
{
    /* border: 1px solid red; */
    position: absolute;
    width: 500px;
    height: 345px;

}
/* open */
.emoji_picker_wrapper.show
{
    position: absolute;
    bottom: 35%;
    left: 63%;
    /* border: 1px solid red; */

    z-index: 99;
    text-align: center;
}
/* close */
.emoji_picker_wrapper.close
{
    display: none;
}
/* box */
.box-emojiPicker.close
{
    display: none;
}
.box-emojiPicker.show
{
    position: absolute;
    /* top: 54%;
    right: 4.7%; */
    transform: rotate(135deg);
    z-index: 100;
    box-shadow: 2px -2px 3px rgb(0, 0, 0, 0.2);

    border-top-width: 6px;
    border-bottom-style: solid;
    border-top-color: #413f42;
    border-left-style: solid;
    border-left-width: 6px;
    border-top-style: solid;
    border-bottom-width: 6px;
    border-right-color: #413f42;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-right-style: solid;
    border-right-width: 6px;
}
