/* emoji toggle */
.emoji_toggle 
{
    position: absolute;
    display: block;

    bottom: 0px;
    right: 15px;
    /* border: 1px   solid red; */

    color: rgb(126, 126, 126);
    font-size: 1.7em;

    transition: .1s;
    cursor: pointer;
}
.emoji_toggle:active
{
    transform: scale(0.95);
}
.emoji_toggle:hover 
{
    color: rgb(179, 179, 179);
}
/* emoji scrollbar */
.emoji-mart-scroll::-webkit-scrollbar-track
{
	box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #413f42;
	border-radius: 10px;
}

.emoji-mart-scroll::-webkit-scrollbar
{
	width: 10px;
	background-color: #413f42;
}

.emoji-mart-scroll::-webkit-scrollbar-thumb
{
	background-color: #AAA;
	border-radius: 10px;
	background-image: -webkit-linear-gradient(90deg,
	 rgba(0, 0, 0, .4) 25%,
	 transparent 25%,
	 transparent 50%,
	 rgba(0, 0, 0, .2) 50%,
	 rgba(0, 0, 0, .4) 75%,
	 transparent 75%,
	 transparent)
}
 
/*! Input Another */
/* input image */
.input_addon_wrapper 
{
    border: 1px solid rgb(109, 109, 109);
    border-radius: 8px;
    box-shadow: 0px 0px 3px rgba(255, 255, 255, 0.1);

    margin: 16px;
    padding: 10px;

    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.input_addon_wrapper .input_image
{
    color: #45bd62;
    width: 26px;
    height: 26px;

    cursor: pointer;
}
/* input feeling */
.input_addon_wrapper .input_feeling
{
    color: #f7b928;
    width: 26px;
    height: 26px;

    cursor: pointer;
}
/* wrapper image and felling */
.input_addon_wrapper span:nth-of-type(1)
{
    margin-right: 10px;
}
.input_addon_wrapper span:nth-of-type(2)
{
    margin-right: 10px;
}
.input_addon_wrapper span
{
    border-radius: 50%;
    /* border: 1px solid red; */
    transition: .1s;

    width: 30px;
    height: 30px;

    display: flex;
