/*! fileUpload */
#fileUpload 
{
    display: none;
}

/*! show image input post */
.image_input_post 
{
    /* border: 1px solid red; */
    position: relative;
    width: 100%;
    height: auto;
    padding: 0px 16px;
    margin-top: 5px;

    display: grid; 
    /* grid-template-columns: auto auto auto auto; */
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    grid-gap: 8px;
}
/* li image */
.image_input_post .li_images 
{
    position: relative;
    /* border: 1px solid red; */
    list-style: none;
    border-radius: 5px;
    box-shadow: 0px 0px 6px rgb(0, 0, 0, 0.5);

    width: 100px;
    height: 100px;
    margin: 0 auto;

    cursor: pointer;
}
.image_input_post .li_images::before
{
    content: '';
    position: absolute;

    top: 0px;
    left: 0px;
    
    background: rgb(0, 0, 0, 0.2);    

    width: 100%;
    height: 100%;
    transition: .1s;
}
.image_input_post .li_images:hover::before
{
    background: rgb(0, 0, 0, 0);    
}
/* li image close icon */
.image_input_post .li_images .li_image_close
{
    position: absolute;
    right: 5px;
    top: 5px;

    color: rgb(197, 197, 197);
    background: rgb(65, 64, 64);
    border-radius: 50%;
    box-shadow: 0px 0px 10px rgb(0, 0, 0, 0.5);

    opacity: 0;
    transition: .1s;
}
.image_input_post .li_images:hover .li_image_close
{
    opacity: 1;
}

/*! li image of type*/
/*todo check length */
.image_input_post .li_images.checkLength:nth-of-type(4)
{
    /* border: 1px solid red; */
    transition: .1s;
    border-radius: 5px;
}
/* after */
.image_input_post .li_images.checkLength:nth-of-type(4)::after
{
    content: '';
    position: absolute;

    top: 0px;
    left: 0px;
    
    background: rgb(0, 0, 0, 0.6);    

    width: 100%;
    height: 100%;
    transition: .2s;
}
/* hover */
.image_input_post .li_images.checkLength:nth-of-type(4):hover::after
{
    background: rgb(0, 0, 0, 0.6);    
}
/*todo n+5 close */
.image_input_post .li_images.checkLength.nPlus5CLose:nth-of-type(n+5)
{
    display: none;
}
/*todo images counting */
.images_counting:nth-of-type(-n+3)
{
    /* border: 1px solid red; */
    display: none;
}
.images_counting:nth-of-type(n+5)
{
    display: none;
}
/* style images counting */
.images_counting 
{
    color: rgb(231, 231, 231);
    font-size: 1.7em;
    border-radius: 5px;

    position: absolute;
    top: 0px;
    right: 21px;
    width: 100px;
    height: 100px;

    /* border: 1px solid red; */

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
}
.images_counting::selection
{
    pointer-events: none;
}
.images_counting::after
{
    content: '';
    position: absolute;

    top: 0px;
    left: 0px;
    
    border-radius: 5px;
    background: rgb(0, 0, 0, 0.4);    

    width: 100%;
    height: 100%;
    transition: .2s;
}
.images_counting:hover::after
{
    background: rgb(0, 0, 0, 0.1);    
}

/*! Alert ID */
.alert_error
{
    font-size: 1em;
    color: rgb(226, 226, 226);
    text-shadow: 1px 1px 6px rgb(255, 255, 255, 0.6);

    cursor: not-allowed;
    z-index: 9999;
}
.alert_error::selection
{
    pointer-events: none;
}

