.main_top .top_input
{
    position: relative;

    width: 100%;
    height: 40px;
    padding: 10px 15px 10px 15px;
    line-height: 20px;
    letter-spacing: 0.8px;
    
    border-radius: 20px;
    border:none;
    outline: none;
    
    background: #282729;
    color: rgb(190, 190, 190);
    font-size: 13px;
    cursor: pointer;
    box-shadow: 0px 0px 3px rgb(0, 0, 0, .3);
}
.main_top .top_input::after 
{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background: #49484866;

    left: 0;
    top: 0;
    transition: .1s;
}
.main_top .top_input::selection
{
    pointer-events: none;
}
.main_top .top_input:hover::after 
{
    opacity: 0;
}
